.overlay {
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: rgba(0, 0, 0, .5);
  position: absolute;
  top: 0;
  left: 0;
}

.modal {
  width: 50%;
  z-index: 3;
  text-align: center;
  background-color: #f5f5f5;
  border-radius: 13px;
  padding: 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hide {
  opacity: 0;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(101%);
}

.modal-close-btn {
  position: absolute;
  top: 5px;
  right: 10px;
}

